import { useMemo, useState } from "react";
import numeral from "numeral";
import {  getTokenDecimals, isAddress, mulBN, subBN, sumBN } from "../helpers";
import { formatBNPrice, getHoldings, getValueRate } from "../helpers";
import useStrategiesFromAccount from "../hooks/useStrategiesFromAccount";
import { DAI_TOKEN, USDC_TOKEN, WETH_TOKEN } from "../constants/tokens";
import useUSDCPrice from "../hooks/useUSDCPrice";

export default function useTotalPortfolioValueViewer(agreementAddress) {
  const [totalLocked, setTotalLocked] = useState(0);
  const [totalGains, setTotalGains] = useState(0);
  const [totalInvested, setTotalInvested] = useState(0);
  const { holdings, invested } =
    useStrategiesBalance(agreementAddress);

  return useMemo(() => {
    setTotalLocked(0);
    setTotalGains(0);
    setTotalInvested(0);
    if (holdings) {
      const totalSum = Object.values(holdings).reduce((sum, tokenValue) => {
        const n = tokenValue ? tokenValue : 0;
        return numeral(sum).add(n).value();
      }, 0);

      const investedSum = Object.values(invested).reduce((sum, tokenValue) => {
        const n = tokenValue ? tokenValue : 0;
        // console.log('totalApy apu', tokenValue, n, sum, sumBN(sum,n))
        return sumBN(sum, n);
      }, 0);

      const gains = subBN(totalSum, investedSum);

      setTotalLocked(totalSum);
      setTotalInvested(investedSum);
      setTotalGains(gains);
      // console.log("useTotalPortfolioValue", totalSum, investedSum, gains);
    }

    return { totalLocked, totalInvested, totalGains };
    // eslint-disable-next-line
  }, [
    holdings,
    agreementAddress,
    totalLocked,
    totalInvested,
    totalGains,
  ]);
}

const useStrategiesBalance = (agreementAddress = null) => {
  const { data, isLoading } = useStrategiesFromAccount(agreementAddress);
  const price = {
    [USDC_TOKEN["137"]]: "1",
    [DAI_TOKEN["137"]]: useUSDCPrice(DAI_TOKEN["1"], 1),
    [WETH_TOKEN["137"]]: useUSDCPrice(WETH_TOKEN["1"], 1),
  };


  return useMemo(() => {
    let totalInvested = {};
    let totalBalance = {};
    // let totalSumApy = {};

    data?.account?.strategies?.forEach(async (st) => {
      const tk = isAddress(st?.strategy?.token?.id);
      const tokenInfo = getTokenDecimals(tk, "137");

      const currentValue = getHoldings(
        st?.shares,
        st?.strategy?.lastRate?.totalShares,
        st?.strategy?.lastRate?.totalValue
      );
      const holdings = getValueRate(
        currentValue,
        st?.strategy?.lastRate?.valueRate
      );

      const initalHoldings = getValueRate(
        st?.invested,
        st?.strategy?.lastRate?.valueRate
      );

      const initalHoldingsPrice = formatBNPrice(
        initalHoldings,
        tokenInfo?.decimals
      );
      const initalHoldingsUSDC = mulBN(initalHoldingsPrice, price[tk]);

      const holdingsPrice = formatBNPrice(holdings, tokenInfo?.decimals);
      const holdingsUSDC = mulBN(holdingsPrice, price[tk]);
      // console.log('holdingsUSDC', holdingsUSDC)

      if (totalInvested.hasOwnProperty(tk)) {

        totalInvested[tk] = sumBN(totalInvested[tk], initalHoldingsUSDC);
        totalBalance[tk] = sumBN(totalBalance[tk], holdingsUSDC);
        // totalSumApy[tk] = sumBN(totalSumApy[tk], apyPerAmount);
      } else {
        console.log("blockNumber a");
        totalInvested[tk] = initalHoldingsUSDC;
        totalBalance[tk] = holdingsUSDC;
        // totalSumApy[tk] = apyPerAmount;
      }
    });

    console.log("useStrategiesBalance totalBalance", totalBalance);
    // console.log("useStrategiesBalance totalInvested",totalInvested);

    return {
      invested: totalInvested,
      holdings: totalBalance,
      sumApy: 0,
      isLoading: isLoading,
    };
    // eslint-disable-next-line
  }, [data]);
};