import { useCoingeckoTokenPrice } from "@usedapp/coingecko";
import { amountPerPrice } from "../helpers";

export default function useUSDCPrice(TOKEN_ADDRESS = undefined, amount = 0, format = '0.0000') {
  const price = useCoingeckoTokenPrice(TOKEN_ADDRESS, "usd");
  return amountPerPrice(amount, price, format);
}
